

body {
    background-color: #404040;
}

.movies {
    display: flex;
    flex-wrap: wrap;
    padding-top: 2%;
    
}
.movies-card {
    display: flex;
    flex-direction: column;
    width: 250px;
    height: 350px;
    margin: 4px auto;
    border-bottom: 2px solid #F5F5F5;
    border-radius: 8px;
    background: #d9d9d9;
    font-family: "Open Sans";
    text-align: center;
    transition: .3s ease-in;
}
.movies-card:hover {
    border-bottom: 2px solid #fc5a5a;
    opacity: 0.7;
}
.movies-info {
    flex: 1 0 auto;
    height: 15%;
    
}
.movies-info h4 {
    margin: 5% 0 10px 0;
    color: #444444;
    font-weight: 400;
}

.details { 
    border: 2px solid #808080;
   border-radius: 8px; 
    background-color: transparent;
    color: #808080;
    text-decoration: none; 
    text-transform: uppercase;
   cursor: pointer;
}
    .details:hover {
        background-color: #D3D3D3;
    }

.movies-image img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: 8px;
}
.movies-image {
    display: block;
    width: 100%;
    height: 75%;
}
.trailer-nothave {
    position: absolute;
    top: 120%;
    margin: 0 auto;
    width: 70%;
}
.header {
    display: flex;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 30px;
    background-color: whitesmoke;
    z-index: 1;
}
.dropdown-movies {
    top: 0;
}
 #search-header {
    position: fixed;
    top: 0;
    right: 0;
    z-index: 100;
}
.itemInfo {
    width: 70%;
    height: 1700px;
    margin: 0 auto;
    background-color: white;
}

.header-button {
    color: black;
    font-size: 120%;
    width: 100px;
    text-decoration: none;
    cursor: pointer;
}


.showItem-image img {
    position: absolute;
    top: 9%;
    width: 30%;
    height: 60%;
    padding-left: 2%;
}

.showItem-overview {
    position: absolute;
    top: 30px;
    margin-left: 30%;
    width: 35%;
    height: 100px;
    padding-left: 3%;
    font-size: 150%;
}
.showItem-info {
    position: absolute;
    top: 70%;
    width: 400px;
    height: 40%;
    padding-left: 2%;
   
}
.showItem-info p {
    font-size: 150%;
}
#search-header input {
    height: 27px;
    border: 0;
    outline: none;
    font-size:120%;
}
#search-header button {
   height: 28px;
   border: none;
   outline: none;
    background: #e8e8e8;
    color: #8b8b8b;
    text-decoration: none;
}
#search-header button:hover {
    background-color: gainsboro;
}
.header button {
    height: 30px;
    border: none;
    cursor: pointer;
}
.header button:hover {
    background-color: gainsboro;
}
.dropdown-movies {
    display: inline-block;
}

.dropdown-content {
    display: none;
    position: absolute;
    background-color: #f9f9f9;
    box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
    z-index: 1;
}

.dropdown-content button {
        display: block;
        color: black;
        text-decoration: none;
        cursor: pointer;
}

.dropdown-movies:hover .dropdown-content {
    display: block;
}
.dropdown-button {
    
    text-align: center;
    width: 100px;
}
.dropdown-button:hover {
        background-color: gainsboro;
}
#showItem-trailer {
    margin: 0 auto;
    width: 70%;
    height: 100%;
    position: absolute;
    top: 120%;
    
}
    #showItem-trailer iframe {
        width: 98.5%;
        height: 60%;
    }

    .link-trailer p {
        width: 70%;
        height: 100%;
        position: absolute;
        top: 120%;
        font-size: 120%;
    }
    .slider-similar-items {
        position: absolute;
        width: 58.2%;
        height: 100%;
        top: 190%;
        display: flex;
        overflow: hidden;
        left: 21%;
    }
#text-similar-items {
        position: absolute;
        left: 42%;
        top: 180%;
    }
    .slider-similar-items .movies-card {
        min-width: 250px;
        padding: 1px;
    }
.btn-slider-prev {
    position: absolute;
    border: 1px solid #f5f5f5;
    border-width: 0 0 2px 2px;
    width: 50px;
    height: 50px;
    line-height: 0;
    font-size: 0;
    transform: rotate(45deg);
    cursor: pointer;
    top: 210%;
    left: 10%;
}
.btn-slider-next {
    position: absolute;
    border: 1px solid #f5f5f5;
    border-width: 0 0 2px 2px;
    width: 50px;
    height: 50px;
    line-height: 0;
    font-size: 0;
    top: 210%;
    right: 10%;
    transform: rotate(-135deg);
    cursor: pointer;
}

